#page-room {
  header {
    padding: 1.5rem;
    border-bottom: 1px solid #e2e2e2;

    .content {
      width: 100%;
      margin: 0 auto;
      display: flex;
      justify-content: space-around;
      flex-wrap: wrap;
      gap: 1rem;
      align-items: center;

      > img {
        max-height: 45px;
      }

      >div {
        display: flex;
        flex-wrap: wrap;
        gap: 1rem;
        
        button {
          height: 40px;
        };
      }
    }
  }

  main {
    max-width: 800px;
    margin: 0 auto;
    padding: 1rem;

    .room-title {
      margin: 32px 0 24px;
      display: flex;
      flex-wrap: wrap;
      gap: 1rem;
      align-items: center;

      h1 {
        font-family: 'poppins', sans-serif;
        font-size: 24px;
        color: #29292e;
      }

      span {
        background-color: #e559f9;
        border-radius: 9999px;
        padding: 8px 16px;
        color: #fff;
        font-weight: 500;
        font-size: 14px;
      }
    }

    form {
      textarea{
        width: 100%;
        border: 0;
        padding: 16px;
        border-radius: 8px;
        background-color: #fefefe;
        box-shadow: 0 2px 12px rgba(0, 0, 0, 0.04);
        resize: vertical;
        min-height: 138px;
      }

      .form-footer {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 0.5rem;
        flex-wrap: wrap;
        margin-top: 1rem;

        .user-info {
          display: flex;
          align-items: center;

          img {
            width: 32px;
            height: 32px;
            border-radius: 50%;
          }

          span {
            margin-left: 8px;
            color: #29292e;
            font-weight: 500;
            font-size: 0.8rem;
          }
        }

        > span {
          font-size: 0.8rem;
          color: #737388;
          font-weight: 500;

          button {
            background: transparent;
            border: 0;
            color: #835afd;
            text-decoration: underline;
            font-size: 0.8rem;
            font-weight: 500;
          }
        }
      }
    }

    .question-list {
      margin-top: 32px;
      
      .no-questions {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: 1rem;

        p {
          color: #737380;
          font-size: 0.8rem;
        }

        strong {
          color: #29292E;
          font-size: 1.2rem;
          font-weight: 600;
        }

      }
    }
  }
}